export default {
	data() {
		return {
			money: ''
		};
	},
	mounted() {
		this.$nextTick(function(){
			this.$http.get('/bargain_v1/user/envelope',{
				params:{
					token: this.$store.getters.token
				}
			})
				.then(res => {
					console.log(res.data);
					if(res.data.code == 1){
						let data = res.data.data;
						this.money = data.money.toFixed(2);
					}
				}).catch(err => {
					console.log(err);
				})
		})
	},
	methods:{
		toCash(){
			if(this.money > 0){
				uni.navigateTo({
					url: '../cash/index'
				})
			}else{
				uni.showToast({
					icon: 'none',
					title:'没有钱怎么提现呢?'
				})
			}
		}
	}
}
